<template>
  <section class="dash-payment-int">
    <sweet-modal ref="paymentDetails" title="Payment Details">
      <h3>
        Document
      </h3>
    </sweet-modal>

    <div class="top">
      <div class="page-heading">
        <h4>Dashboard > {{ $route.name }}</h4>
      </div>
    </div>

    <table class="table table-hover table-stripe">
      <thead class="thead-colored">
        <tr>
          <th scope="col">#</th>
          <th scope="col">Status</th>
          <th scope="col">Amount</th>
          <th scope="col">Date</th>
          <th scope="col">Host</th>
          <th scope="col">Guest</th>
          <th scope="col">Details</th>
        </tr>
      </thead>
      <transition name="fade">
        <tbody v-if="pays">
          <tr v-for="(user, index) in pays" :key="index">
            <td scope="row">{{ index }}</td>
            <td class="status-col" v-if="index == 0">
              <span class="danger-dot"></span>
              Canceled
            </td>
            <td class="status-col" v-if="index == 1">
              <span class="pending-dot"></span>
              Pending
            </td>
            <td class="status-col" v-if="index > 1">
              <span class="success-dot"></span>
              Success
            </td>
            <td>$1,234</td>
            <td>
              1/1/2020, 12:30
            </td>
            <td>host_1</td>
            <td>
              guest_1
            </td>
            <td class="form-btn" @click="showSweetModalDetails">
              Details
            </td>
          </tr>
        </tbody>
      </transition>
    </table>
  </section>
</template>

<script>
export default {
  data() {
    return {
      pays: [0, 1, 2, 3, 4, 5],
    };
  },
  methods: {
    showSweetModalDetails() {
      this.$refs.paymentDetails.open();
    },
  },
};
</script>

<style scoped lang="scss">

.status-col {
  display: flex;
  align-items: center;
  justify-content: center;
}
.danger-dot,
.pending-dot,
.success-dot {
  width: 10px;
  height: 10px;
  display: block;
  border-radius: 50%;
  margin-right: 4px;
}
.danger-dot {
  background-color: $danger;
}
.pending-dot {
  background-color: $pending;
}
.success-dot {
  background-color: $success;
}
.form-btn {
  cursor: pointer;
}
.thead-colored {
  background-color: $primary;
  color: $white;
}
</style>
